import {
  JupyterFrontEnd,
  JupyterFrontEndPlugin
} from '@jupyterlab/application';
import {
  ICommandPalette,
  IThemeManager,
  IToolbarWidgetRegistry
} from '@jupyterlab/apputils';

import { INotebookTracker } from '@jupyterlab/notebook';
import { ILauncher } from '@jupyterlab/launcher';

import { tokens, CoreExtension, PanelPluginOpts } from '@anaconda/shared';

import { PanelPlugin } from '@anaconda/panel_lib';

const panelPlugin = new PanelPlugin();

/**
 * Initialization data for the @anaconda/panels extension.
 */
const plugin: JupyterFrontEndPlugin<void> = {
  id: '@anaconda/panels:plugin',
  description: 'A JupyterLab extension.',
  autoStart: true,
  requires: [
    tokens.CoreToken,
    ILauncher,
    INotebookTracker,
    ICommandPalette,
    IThemeManager,
    IToolbarWidgetRegistry
  ],
  activate: (
    app: JupyterFrontEnd,
    coreExtension: CoreExtension,
    launcher: ILauncher,
    notebookTracker: INotebookTracker,
    palette: ICommandPalette,
    themeManager: IThemeManager,
    toolbarRegistry: IToolbarWidgetRegistry
  ) => {
    console.log('@anaconda/panels:plugin extension is activated');
    const { corePlugin } = coreExtension;
    const panelOpts: PanelPluginOpts = {
      themeManager,
      corePlugin,
      notebookTracker,
      palette,
      toolbarRegistry,
      launcher
    };

    panelPlugin.activate(app, panelOpts);
  }
};

export default plugin;
